import React, { useState, useEffect } from 'react';
import { Box, Typography, TextField, MenuItem } from '@mui/material';
import { useLanguage } from '../../contexts/LanguageContext';
import { useTemplate } from '../../contexts/TemplateContext';
import { templates, templateNames } from '../../templates';

const GeneralTab: React.FC<{ updateTemplateField: (field: string, value: any) => void }> = ({ updateTemplateField }) => {
  const { language } = useLanguage();
  const { state, dispatch } = useTemplate();
  const [selectedTemplate, setSelectedTemplate] = useState('New Template');

  //  selectedTemplate   template
  useEffect(() => {
    //  ,    
    const currentTemplateJson = JSON.stringify(state.template);
    const matchingTemplate = templateNames.find(name => 
      JSON.stringify(templates[name]) === currentTemplateJson
    );
    
    if (matchingTemplate) {
      setSelectedTemplate(matchingTemplate);
    } else {
      //      ,  Empty Template
      setSelectedTemplate('New Template');
    }
  }, [state.template]);

  const handleTemplateSelect = (templateKey: string) => {
    setSelectedTemplate(templateKey);
    
    if (templateKey && templates[templateKey]) {
      //   
      dispatch({ type: 'SET_TEMPLATE', payload: templates[templateKey] });
    }
  };

  const handleTemplateNameChange = (name: string) => {
    updateTemplateField('TemplateName', name);
    //       Empty Template
    if (selectedTemplate !== 'New Template') {
      setSelectedTemplate('New Template');
    }
  };

  const handleNumberChange = (field: string, value: string) => {
    if (value === '') {
      updateTemplateField(field, undefined);
    } else {
      const numValue = Number(value);
      if (!isNaN(numValue)) {
        updateTemplateField(field, numValue);
      }
    }
  };

  return (
    <Box>
      <Typography variant="h5" gutterBottom>
        {language === 'ru' ? ' ' : 'General Settings'}
      </Typography>
      
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3, maxWidth: 600 }}>
        {/*   */}
        <Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {language === 'ru' ? ' ' : 'Template Selection'}
            </Typography>
          </Box>
          <TextField
            select
            value={selectedTemplate}
            onChange={(e) => handleTemplateSelect(e.target.value)}
            fullWidth
          >
            {templateNames.map((name) => (
              <MenuItem key={name} value={name}>
                {name}
              </MenuItem>
            ))}
          </TextField>
        </Box>

        {/*   */}
        <Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {language === 'ru' ? ' ' : 'Template Name'}
            </Typography>
          </Box>
          <TextField
            value={state.template.TemplateName || ''}
            onChange={(e) => handleTemplateNameChange(e.target.value)}
            fullWidth
            placeholder={language === 'ru' ? "  " : "Enter template name"}
          />
        </Box>

        {/*     */}
        <Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {language === 'ru' ? '   ' : 'Wood and Ore Mines Guard Level'}
            </Typography>
          </Box>
          <TextField
            type="number"
            value={state.template.GeneralData?.Mine1LevelGuardLevel ?? ''}
            onChange={(e) => handleNumberChange('GeneralData.Mine1LevelGuardLevel', e.target.value)}
            fullWidth
            inputProps={{ min: 0 }}
            placeholder={language === 'ru' ? "     " : "Wood and Ore Mines Guard"}
          />
        </Box>

        {/*     */}
        <Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {language === 'ru' ? '   ' : 'Rare Resources Mines Guard Level'}
            </Typography>
          </Box>
          <TextField
            type="number"
            value={state.template.GeneralData?.Mine2LevelGuardLevel ?? ''}
            onChange={(e) => handleNumberChange('GeneralData.Mine2LevelGuardLevel', e.target.value)}
            fullWidth
            inputProps={{ min: 0 }}
            placeholder={language === 'ru' ? "     " : "Rare Resources Mines Guard"}
          />
        </Box>

        {/*    */}
        <Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {language === 'ru' ? '  ' : 'Gold Mines Guard Level'}
            </Typography>
          </Box>
          <TextField
            type="number"
            value={state.template.GeneralData?.MineGoldGuardLevel ?? ''}
            onChange={(e) => handleNumberChange('GeneralData.MineGoldGuardLevel', e.target.value)}
            fullWidth
            inputProps={{ min: 0 }}
            placeholder={language === 'ru' ? "    " : "Gold Mines Guard"}
          />
        </Box>

        {/*     */}
        <Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {language === 'ru' ? '   ' : 'Hero Base Movement Points'}
            </Typography>
          </Box>
          <TextField
            type="number"
            value={state.template.GeneralData?.HeroBaseMoves ?? ''}
            onChange={(e) => handleNumberChange('GeneralData.HeroBaseMoves', e.target.value)}
            fullWidth
            inputProps={{ min: 0 }}
            placeholder={language === 'ru' ? "  " : "Hero Base Movement"}
          />
        </Box>

        {/*    */}
        <Box>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {language === 'ru' ? '  ' : 'Base Army Multiplier'}
            </Typography>
          </Box>
          <TextField
            type="number"
            value={state.template.BaseArmyMultiplier ?? 1.0}
            onChange={(e) => updateTemplateField('BaseArmyMultiplier', parseFloat(e.target.value) || 1.0)}
            fullWidth
            inputProps={{ step: 0.1, min: 0.1, max: 10 }}
          />
        </Box>
      </Box>
    </Box>
  );
};

export default GeneralTab;