import React from 'react';
import {
  Box,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Typography,
  Divider
} from '@mui/material';
import {
  Settings as SettingsIcon,
  Map as MapIcon,
  Link as LinkIcon,
  Terrain as TerrainIcon,
  Business as BuildingIcon,
  MilitaryTech as MilitaryTechIcon,
  Code as CodeIcon,
  Block as BlockIcon,
  AutoAwesome as AutoAwesomeIcon,
  Architecture as ArchitectureIcon,
  Shuffle as ShuffleIcon,
  Preview as PreviewIcon
} from '@mui/icons-material';

interface SidebarProps {
  selectedTab: number;
  onTabChange: (tabIndex: number) => void;
  language: 'ru' | 'en'; //    
}

//       
const menuItems = [
  { 
    en: 'General', 
    ru: '', 
    icon: <SettingsIcon /> 
  },
  { 
    en: 'Zones', 
    ru: '', 
    icon: <MapIcon /> 
  },
  { 
    en: 'Connections', 
    ru: '', 
    icon: <LinkIcon /> 
  },
  { 
    en: 'Terrains', 
    ru: '', 
    icon: <TerrainIcon /> 
  },
  { 
    en: 'Start Buildings', 
    ru: ' ', 
    icon: <BuildingIcon /> 
  },
  { 
    en: 'Army', 
    ru: '', 
    icon: <MilitaryTechIcon /> 
  },
  { 
    en: 'Script Features', 
    ru: '. ', 
    icon: <CodeIcon /> 
  },
  { 
    en: 'Entities Ban', 
    ru: '', 
    icon: <BlockIcon /> 
  },
  { 
    en: 'Start Spells', 
    ru: ' ', 
    icon: <AutoAwesomeIcon /> 
  },
  { 
    en: 'Custom Building', 
    ru: ' ', 
    icon: <ArchitectureIcon /> 
  },
  { 
    en: 'Zone Randomization', 
    ru: ' ', 
    icon: <ShuffleIcon /> 
  },
  { 
    en: 'JSON Preview', 
    ru: ' JSON', 
    icon: <PreviewIcon /> 
  }
];

const Sidebar: React.FC<SidebarProps> = ({ selectedTab, onTabChange, language }) => {
  //       
  const getText = (item: { en: string; ru: string }) => {
    return language === 'ru' ? item.ru : item.en;
  };

  //    
  const getTitle = () => {
    return language === 'ru' 
      ? 'Heroes 5 Lobby - Template Editor'
      : 'Heroes 5 Lobby - Template Editor';
  };

  return (
    <Box
      sx={{
        width: 280,
        minWidth: 280,
        bgcolor: '#f8f9fa',
        borderRight: '1px solid #e0e0e0',
        height: '100vh',
        overflowY: 'auto'
      }}
    >
      {/*  */}
      <Box sx={{ p: 2, bgcolor: '#F1BF11', color: '#000' }}>
        <Typography variant="h6" fontWeight="bold">
          {getTitle()}
        </Typography>
      </Box>

      <Divider />

      {/*  */}
      <List sx={{ p: 1 }}>
        {menuItems.map((item, index) => (
          <ListItem key={index} disablePadding>
            <ListItemButton
              selected={selectedTab === index}
              onClick={() => onTabChange(index)}
              sx={{
                borderRadius: 1,
                mb: 0.5,
                '&.Mui-selected': {
                  bgcolor: '#F1BF11',
                  color: '#000',
                  '&:hover': {
                    bgcolor: '#E0AE10',
                  },
                },
                '&:hover': {
                  bgcolor: '#eee',
                },
              }}
            >
              <ListItemIcon sx={{ color: selectedTab === index ? '#000' : 'inherit' }}>
                {item.icon}
              </ListItemIcon>
              <ListItemText 
                primary={getText(item)}
                primaryTypographyProps={{
                  fontSize: '14px',
                  fontWeight: selectedTab === index ? 'bold' : 'normal'
                }}
              />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );
};

export default Sidebar;